@import "../../../styles/_breakpoint";
@import "../../../styles/_mixin";

@font-face {
  font-family: "Onyx";
  src: url(../../../fonts/NIRVANA.TTF) format("truetype");
}

.music-year-block{
  margin-bottom: 70px;

  @include mq(sm) {
    margin-bottom: 40px;
  }
}

.albums-year{
  color:white;
  text-align: center;
  font-family: Onyx, serif;
  font-size: 35px;

  @include mq(sm) {
    margin: 20px 0;
  }
}

.albums-rows{
  display: flex;
  flex-wrap: wrap;
  row-gap: 40px;
}

.album-cover-container{
  flex: 0 0 auto;
  width: 25%;
  height: 175px;
  text-align: center;

  @include mq(sm) {
    width: 50%;
    height: 145px;
  }
}

.album-cover {
  height: 170px;
  transition: all 0.5s ease-in-out;

  @include mq(sm) {
    height: 140px;
  }
}

.album-cover:hover {
  cursor: pointer;
  height: 175px;
}